<template>
  <div>
    <div class="img-div-nopatientinfo">
      <img
        class="img-nopatient"
        src="../../assets/img/iconfont/nopatientinfo.png"
      />
    </div>
    <div class="div-spanlabel">
      <span>
        <label class="label-nopatient">还未添加就诊人</label>
      </span>
    </div>
    <div class="div-elbutton">
      <el-button type="primary" style="width:150px" @click="addPatient"
        >立即添加</el-button
      >
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    addPatient() {
      this.$router.push({ path: "/shopcart/department/addinfopatient" });
    }
  }
};
</script>
<style scoped>
@import "../../assets/css/gshawn.css";
</style>



